.formulario {
  p {
    font-size: 1.4rem;
    color: $grisOscuro;
    margin: 2rem 0 0 0;
  }

  legend {
    font-size: 2rem;
    color: $grisOscuro;
  }

  label {
    display: block;
    font-size: 1.4rem;
    color: $grisOscuro;
    font-weight: $bold;
    text-transform: uppercase;
  }

  input:not([type='submit']),
  textarea,
  select {
    width: 100%;
    padding: 1rem;
    margin-bottom: 2rem;
    border: 1px solid $gris;
    border-radius: 1rem;
    font-size: 1.4rem;
    display: block;
    background-color: $blanco;
  }

  .forma-contacto {
    max-width: 30rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  input[type='radio'] {
    margin: 0;
    width: auto;
  }

  fieldset {
    margin-bottom: 2rem;
    border: 1px solid $grisOscuro;
    padding: 2rem;
    border-radius: 1rem;
  }
}
